@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #f9fbfd;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

html {
  scroll-behavior: smooth;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1366px;
  margin: 0 auto;
  padding-left: 103px;
  padding-right: 103px;
}
@media (max-width: 1439px) {
  .container {
    padding-left: 103px;
    padding-right: 103px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  padding: 10px 47px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
.btn:hover, .btn:focus {
  background-color: #cfe2f3;
}
.btn:active {
  background-color: #9fc5e8;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.logo svg {
  color: #f9fbfd;
  vertical-align: top;
  cursor: pointer;
}
.logo svg:hover, .logo svg:focus {
  color: #6fa8dc;
}
.logo svg:active {
  color: #45818e;
}
@media (max-width: 1023px) {
  .logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo svg {
    width: 42px;
    height: 22px;
  }
}

@keyframes slide-in {
  0% {
    margin-left: 100vw;
  }
  100% {
    margin-left: 0;
  }
}
.header {
  position: absolute;
  z-index: 1;
  width: 100%;
}
@media (max-width: 767px) {
  .header.is-open {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    background-color: transparent;
  }
  .header.is-open .header__container {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto 1fr;
    row-gap: 24px;
    padding-left: 51px;
    background-color: #f9fbfd;
    clip-path: inset(0 0 0 36px);
    animation: slide-in 0.3s;
  }
  .header.is-open .header__logo {
    justify-self: flex-start;
  }
  .header.is-open .header__logo svg {
    color: #011c40;
  }
  .header.is-open .header__toggle {
    justify-self: flex-end;
    margin-right: -15px;
  }
  .header.is-open .header__toggle-line {
    background-color: transparent;
  }
  .header.is-open .header__toggle-line::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .header.is-open .header__toggle-line::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .header.is-open .header__toggle-line::before, .header.is-open .header__toggle-line::after {
    top: 50%;
    left: 50%;
    width: 18px;
    background-color: #011c40;
  }
  .header.is-open .header__navigation {
    display: flex;
    grid-column: 1/-1;
  }
  .header.is-open .header__navigation-list {
    width: 100%;
    padding-right: 0;
    padding-left: 0;
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  height: 100%;
  padding-top: 35px;
  padding-bottom: 35px;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.header__navigation {
  padding-top: 10px;
}
@media (max-width: 1023px) {
  .header__navigation {
    padding-top: 1px;
  }
}
@media (max-width: 767px) {
  .header__navigation {
    display: none;
    width: 100%;
  }
}

.header__navigation-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 34px;
}
@media (max-width: 1023px) {
  .header__navigation-list {
    column-gap: 24px;
  }
}
@media (max-width: 767px) {
  .header__navigation-list {
    flex-direction: column;
    column-gap: 0;
    row-gap: 10px;
    padding: 0 30px 0 15px;
  }
}

.header__navigation-link {
  display: block;
  cursor: pointer;
  font-weight: 500;
  line-height: 18px;
}
.header__navigation-link:hover, .header__navigation-link:focus {
  color: #6fa8dc;
}
.header__navigation-link:active {
  color: #45818e;
}
@media (max-width: 1023px) {
  .header__navigation-link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .header__navigation-link {
    position: relative;
    display: block;
    padding: 10px 0 8px 10px;
    color: #011c40;
    font-size: 18px;
    line-height: 22px;
  }
}

.header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    margin-top: -9px;
    margin-right: -8px;
    padding: 0;
    background-color: transparent;
    border: none;
    box-sizing: border-box;
    cursor: pointer;
  }
}

.header__toggle-line {
  position: relative;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  transition: 0.2s all;
  transform-origin: left center;
}
.header__toggle-line::before, .header__toggle-line::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  transform-origin: center;
  transition: 0.2s all;
}
.header__toggle-line::before {
  top: 6px;
}
.header__toggle-line::after {
  top: -6px;
}

.hero {
